	module.exports = {"data":{"post":{"html":"<p>(Anyone familiar with current children's videos will know from the headline what movie Christopher has been watching constantly the past two weeks.)</p>\n<p>We're still in Florida, but have been following events in Iraq via the St. Petersburg Times and occasional fleeting glimpses at TV. When the newspaper headline is all upper case, we can tell that it was a big day.</p>\n<p>The TV critic for the Times, Eric Deggens, had a nice observation about Fox News today: \"Roger Ailes... has excelled by capitalizing on an essential truth about American TV viewers: Often we say we want one thing, when we really want another. We may say we want objective coverage, but we really want news that fits our own cultural world view.\" I don't think that applies only to American viewers...</p>\n<p>I'll have to see if I can pick up that Kagan book that <a href=\"http://ahawkins.org/comments.php?id=P1064_0_1_0\">Alwin Hawkins recommended</a> before we leave. The Times had a good review of the book and an Op-Ed by him as well (Kagan, not Hawkins).</p>","fields":{"title":"Just A Note To Say...","date":"15 Apr 2003","url":"https://papascott.de/2003-04-15-just-a-note-to-say/"}}},"pathContext":{"slug":"/2003-04-15-just-a-note-to-say/","prev":{"fields":{"title":"Rockaway Beach","date":"04 Apr 2003","slug":"/2003-04-04-rockaway-beach/"}},"next":{"fields":{"title":"We're Back","date":"19 Apr 2003","slug":"/2003-04-19-were-back/"}}}}

